/* 
	去除每个按钮的轮廓阴影
	 */
	.delete-shadow {
		border: none;
	}
	/* 
	清除浮动通用样式
	 */
	.clear {
		clear: both;
	}
	/* 
	表格的样式(包含删除、修改等操作按钮的样式)

	 */
	.table-container {
		table.basic-table {
			font-size: 14px;
			tr,td,th {
				text-align: center;
			}
			tr {
				line-height: 40px;
				vertical-align: middle;
				border-bottom: 4px solid #fff;
			}
			tbody tr:hover {
				background-color: #c2d7ea;
			}
			//删除、修改等操作按钮的样式
			input {
				@extend .delete-shadow;
				color: #28558c;
				font-weight: bolder;
				line-height: 30px;
				border-bottom: 1px solid #28558c;
				background-color: transparent;
				outline: none;
				margin-right: 5px;
			}
		}
		.table-subcontainer-left {
			float: left;
			width: 1100px;
			table {
				width: 100%;
				max-height: 200px;
				overflow-y: auto;
				font-size: 14px;
			}
		}
		.table-subcontainer-center {
			table {
				width: 100%;
				max-height: 200px;
				overflow-y: auto;
				margin-bottom: 15px;
				tbody tr.unchecked td:first-child {
					background: url(../images/datacenter/unselect.gif) no-repeat 6px;
				}
			}			
		}
		table.table-center-whitebgd {
			tbody tr {
				background-color: #fff;
				border-bottom: 4px solid $module-bgd;
			}	
		} 
		table.table-center-bluebgd {
			tbody tr {
				background-color: $module-bgd;
				border-bottom: 4px solid #fff;
			}	
		}		
		.table-button {
			height: 200px;
			width: 26px;
			float: right;
			position: relative;
			right: 20px;
			img:first-child {
				position: absolute;
				top: 45px;				
			} 
			img:last-child {
				position: absolute;
				top: 100px;				
			}
		}	
	}
	/* 
	块容器通用样式
	 */
	.section-container {
		box-sizing: border-box;
		min-height: 315px;
		margin-bottom: 20px;
		padding: 20px 38px;
		background-color: $module-bgd;
		border-radius: 5px;
	}
	/* 
	容器内大标题通用样式
	 */
	.section-title {
		width: 190px;
		height: 25px;
		font-size: 20px;
		font-weight: bold;
		letter-spacing: 2px;
		margin-bottom: 20px;
		padding: 2px;
		border-bottom: 4px solid $underline;
		img {
			margin-right: 10px;
			vertical-align: middle;
		}
		span {
			vertical-align: middle;
			letter-spacing: 2px;
		}
	}
	.add-title {
		width: 600px;
		border: none;
	}
	/* 
	容器内小标题通用样式
	 */
	.section-item {
		margin-bottom: 20px;
		margin-left: 40px;	
		img {
			margin-right: 10px;
			vertical-align: middle;
		}
		span {
			font-size: 16px;
			vertical-align: middle;
			display: inline-block;
			letter-spacing: 2px;
		}
		input {
			width: 180px;
			font-size: 14px;
			border: 1px solid #000;
		}
		textarea {
			height: 65px;
			width: 600px;
			font-size: 14px;
			margin-left: 3px;
			border: 1px solid #000;
			vertical-align: middle;
		}
		select {
			width: 250px;
			font-size: 14px;
			margin: 0px 20px 0px 2px;
			border: 1px solid #000;
		}
	}
	/* 
	容器内操作命令小标题通用样式
	 */
	.section-item-operate {	
		margin-left: 20px;	
	}
	/* 
	包含操作命令按钮的块容器的样式
	 */
	.button-set{
		margin-top: 10px;
	}

	input[type="button"].section-button {
		@extend .delete-shadow;
		font-family: $heiti;
		font-size: 100%;
		width: 170px;
		height: 40px;
		border-radius: 5px;
		background-color: $btn-bgd;
		text-align: center;
		cursor: pointer;
		margin-right: 15px;
	}
	input[type="button"].add-button {
		margin-left: 38px;
		margin-bottom: 20px;
	}
	input[type="button"].index-button {
		display: block；
	}
	input[type="button"].confirm-button {
		width: 80px;
		height: 30px;
	}
	input.author-default {
		margin-left: -20px;
		font-family: $heiti;
		color: #bebfc1;
		border: none;  
		background-color: transparent; 
	}
	
	input.set-btn1 {
		background: url(../images/datacenter/advancedparam.png) no-repeat 4px center;
	}
	input.set-btn2 {
		background: url(../images/datacenter/advancedparam.png) no-repeat 16px center;
	}
	input.modify-btn {
		background: url(../images/datacenter/modify.png) no-repeat 4px center;
	}
	input.delete-btn {
		background: url(../images/datacenter/delete.png) no-repeat 8px center;
	}
	input.new-table-btn {
		background: url(../images/datacenter/new-table.png) no-repeat 8px center;
	}
	input.include-btn {
		background: url(../images/datacenter/include.png) no-repeat 8px center;
	}
	input.index-btn {
		background: url(../images/datacenter/index.png) no-repeat 16px center;
	}
	input.add-table-btn {
		background: url(../images/datacenter/add-table.png) no-repeat 8px center;
	}
	input.store-btn {
		background: url(../images/datacenter/store.png) no-repeat 12px center;
	}
	input.key-btn {
		background: url(../images/datacenter/key.png) no-repeat 0px center;
	}
	input.new-field-btn {
		background: url(../images/datacenter/new-field.png) no-repeat 8px center;
	}
	input.sure-btn {
		background: url(../images/datacenter/sure.png) no-repeat 8px center;
	}
	input.manage-btn {
		background: url(../images/datacenter/manage.png) no-repeat 0px center;
	}
	
	.vm-section {
		padding: 20px 38px;
		border-top: 1px solid #000;
		.section-item {
			padding-left: 60px;
			input {
				width: 150px;
				margin: 0px 40px 0px 10px;
				background-color: #c2d7ea;
				outline: none;
				border: none;
			}
		}
	}
	/* 
	数据导入弹出框 通用样式
	*/
	.popup {
		position: absolute;
		top: 50%;
		left: 50%;
		margin-left: -340px;
		margin-top: -310px;
		width: 680px;
		height: 620px;
		border: 1px solid #bebfc1;
		background-color: #fff;
		.header{
			width: 100%;
			height: 66px;
			background-color: #54606e;//grey
			text-align: center;
			color: #fff;
			span {
				line-height: 66px;
				font-size: 20px;
				font-weight: bolder;
			}
		}
		.content-top{
			margin: 18px 18px 0 18px;
			height: 34px;
			vertical-align: middle;
			select {
				width: 270px;
				height: 100%;
				margin: 0 18px;
				background-color: $module-bgd;
			}
			label {
				padding-left: 20px;
				cursor: pointer;
				background: url(../images/datacenter/unselect.gif) no-repeat left;
			}
			label.checked {
				background-image: url(../images/datacenter/select.gif);
			}
		}
		.content-bottom {
			margin: 18px;
			.content-table{
				padding: 15px 10px;
				margin-bottom: 10px;
				background-color: $module-bgd;
				table {	
					width: 100%;			
					th {
						background-color: $module-bgd;
					}
					tbody tr {
						border-bottom: 4px solid $module-bgd;
						background-color: #fff;
					}
					tbody tr:hover {
						background-color: #54606e; 
					}
					select {
						width: 200px;
						margin-left: 130px;
					}
				}
			}
		}
		.footer {
			margin: 18px 5px 0 5px;
			padding: 8px 28px;
			border-top: 1px solid #54606e; 
		}
		input.next {
				margin-left: 575px;
		}	
		input.popup-btn {
			margin-right: 20px;
			padding: 5px 18px;
			line-height: 30px;
			background-color: #54606e;
			color: #fff;
			border-radius: 5px;
		}			

	}
	/* 
	地图容器
	 */
	.map-container {
		margin-bottom: 20px;
		height: 900px;
		border: 2px solid $btn-bgd; 
		#Iframe {
			width: 100%;
			height: 100%;
		}
	}