	input.build-button {
		@extend .delete-shadow;
		@extend .section-button;
		position: relative;
		left: 900px;
		margin-bottom: 40px;
		width: 100px;		
	}

	// 权限设置弹出框
	.right-set {
		position: absolute;
		width: 340px;
		height: 460px;
		top: 50%;
		left: 50%;
		margin-left: -170px;
		margin-top: -520px;
		background-color: #fff;
		border: 1px solid #272e40;
		.header {	
			text-align: center;
			height: 50px;
			background-color: #272e40;
			span {
				line-height: 50px;
				color: #fff;
				font-size: 24px;
			}
		}
		.right-box {
			background-color: $module-bgd;
			margin: 10px;
			height: 240px;
			.left {
				border-right: 1px solid $underline;
				width: 220px;
				height: 100%;
				float: left;
				img {
					float: left;
					vertical-align: middle;
				}
				ul {
					margin: 10px;
					line-height: 24px;
					.team {
						vertical-align: middle;
						margin: 10px 10px;
					}
					.group {
						vertical-align: middle;
						margin: 10px 25px;
					}
					.person {
						vertical-align: top;
						margin: 10px 40px;
					}
				}
			}
			.right {
				float: left;
				width: 80px;
				padding: 30px 0px;
				input {
					display: inline-block;
					float: left;
					margin: 10px 5px 10px 20px;
				}
				label{
					line-height:33px;
				}			
			}
		}
		.ip-set {
			padding: 10px;
			.top {
				font-size: 18px;
			}
			.content {
				height: 30px;
				margin: 10px 10px;
				background-color: $module-bgd;
				span {
					width: 70px;
					display: inline-block;
					text-align: center;
					line-height: 30px;
					background-color: #c2d7ea;
				}
				input {
					margin: 0px 5px;
				}
				img {
					width: 20px;
					height: 20px;
					margin: 0px 5px;
					vertical-align: middle;
				}
			}
			.bottom {
				margin-left: 140px;
				input {
					color: #fff;
					width: 45px;
					margin-right: 8px;
					background-color: #272e40;
					border: none;
					border-radius: 3px;
				}
			}
		}
	}
}