#new21{
	.table-container .table-subcontainer-center table {
		width: 100%;
	}
	.button-set {
		margin-top: 10px;
	}
	.map-container {
		height: 900px;
		border: 2px solid $btn-bgd; 
	}

	//- 索引弹出框
	.right-set {
		position: absolute;
		width: 340px;
		height: 460px;
		top: 50%;
		left: 50%;
		margin-left: -170px;
		margin-top: -520px;
		background-color: #fff;
		border: 1px solid #272e40;
		.header {	
			text-align: center;
			height: 50px;
			background-color: #272e40;
			span {
				line-height: 50px;
				color: #fff;
				font-size: 24px;
			}
		}
		.switch-btn {
			margin: 10px 20px;
			input {
				width: 80px;
				height: 30px;
				padding: 2px;
				border: 1px solid #272e40;
				border-radius: 5px;
				cursor: pointer;
			}
			input.new {
				background: #272e40;
        font-family: $heiti;
        color: #fff;
			}
			input.exist {
				background: #fff;
        font-family: $heiti;
        color: #272e40;
			}
		}
		.right-box {
			background-color: $module-bgd;
			margin: 10px;
			height: 240px;
			padding: 5px;
			ul {
				li.list-title {
					margin: 5px;
					line-height: 35px;
				}
				li.list-content {
					height: 35px;
					line-height: 35px;
					margin: 5px;
					padding-left: 10px;
					background-color: #fff;
				}
			}
      table.index-table {
        width: 100%;
        min-height: 180px;
        thead {
          line-height: 30px;
        }
        tbody {
          tr {
            line-height: 30px;
            margin-top: 5px;
            background-color: #fff;
            img {
              width: 16px;
              height: 16px;
              margin-top: 8px;
              margin-left: 8px; 
            }
          }
        }
      }
		}
		.field-select {
			margin: 5px 15px;
			select {
				margin-left: 10px;
				width: 260px;
				height: 32px;
			}
		}
		.button {
			margin: 5px 0px 5px 180px ;
			input {
				width: 63px;
        height: 25px;
				color: #fff;
				margin-right: 8px;
				background-color: #272e40;
				border: none;
				border-radius: 3px;
			}
			input.addbutton {
				background-color: #929eaa;
			}
      input.sure-button {
        float: right;
        margin-right: 20px;
      }
		}
	}
}
